import {
  Box,
  Center,
  Grid,
  HStack,
  Icon,
  Text,
  VStack
} from '@chakra-ui/react';
import ListingCardImages from './Images';
import { ListingType } from '@/src/pages/Listings/dummy_data';
import { Amenities } from '../Amenities';
import { PiCardholder, PiMapPin } from 'react-icons/pi';
import { FaCircle } from 'react-icons/fa';
import { formatToCurrency } from '@veerge/utils';

import { fixIcon } from '../../../utils/helpers/fix-icon';

const CardholderIcon = fixIcon(PiCardholder);
const MapPinIcon = fixIcon(PiMapPin);
const FaCircleIcon = fixIcon(FaCircle);

export const ListingCard = (props: ListingType) => {
  return (
    <Box
      width='100%'
      bg='white'
      border='1px solid'
      borderColor='#E4E4E7'
      rounded='12px'
      overflow='hidden'
    >
      <HStack
        width='100%'
        gap='0'
        flexDirection={{ xl: 'row', base: 'column' }}
      >
        <ListingCardImages
          images={[
            'https://images.unsplash.com/photo-1580587771525-78b9dba3b914?q=80&w=774&auto=format&fit=crop&ixlib=rb-4.1.0&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D'
          ]}
        />
        <ListingCardDetails {...props} />
      </HStack>
    </Box>
  );
};

const ListingCardDetails = ({
  listing_name,
  listing_address,
  listing_currency,
  listing_price,
  commission_percentage,
  payment_plan_availability,
  available_units
}: ListingType) => {
  return (
    <VStack
      align='flex-start'
      justify='space-between'
      flexDirection={{ md: 'column', base: 'column-reverse' }}
      width='100%'
      gap={{ md: '20px', base: '0' }}
      flex='1'
      p={{ xl: '18px 16px 18px 24px', md: '16px 16px 24px', base: '0' }}
    >
      <VStack
        width='100%'
        align='flex-start'
        gap='24px'
        p={{ md: '0', base: '16px 16px 24px' }}
      >
        <ListingNameLocationAndPrice
          name={listing_name}
          address={listing_address}
          currency={listing_currency}
          price={listing_price}
          commission={commission_percentage}
        />
        <Box width='100%' display={{ md: 'block', base: 'none' }}>
          <Amenities />
        </Box>
      </VStack>
      <PaymentPlanAvailabilityAndUnits
        isPaymentPlanAvailable={payment_plan_availability}
        availableUnits={available_units}
      />
    </VStack>
  );
};

const ListingNameLocationAndPrice = ({
  name,
  address,
  currency,
  price,
  commission
}: {
  name?: string;
  address?: string;
  currency?: string;
  price: number;
  commission?: number;
}) => {
  const formattedPrice = formatToCurrency({ amount: price });

  return (
    <Grid
      width='100%'
      gridTemplateColumns='1fr auto'
      columnGap={{ md: '32px', base: '16px' }}
      rowGap={{ md: '8px', base: '4px' }}
    >
      <Center justifyContent='flex-start'>
        <Text
          fontSize={{ xl: '28px', md: '20px', base: '16px' }}
          noOfLines={1}
          fontWeight='500'
          lineHeight='100%'
          letterSpacing='-1.1%'
          color='#27272A'
        >
          {name || 'Listing'}
        </Text>
      </Center>
      <Center justifyContent='flex-end' textAlign='right'>
        <Text
          fontSize={{ xl: '28px', md: '20px', base: '16px' }}
          noOfLines={1}
          fontWeight='600'
          lineHeight='100%'
          letterSpacing='-1.1%'
          color='#27272A'
        >
          {formattedPrice}
        </Text>
      </Center>
      <Center justifyContent='flex-start' gap='4px' color='#52525B'>
        <Center boxSize='20px' minWidth='20px' fontSize='20px'>
          <MapPinIcon />
        </Center>
        <Text
          fontSize={{ md: '14px', base: '13px' }}
          fontWeight='400'
          lineHeight='100%'
          letterSpacing='-1.1%'
          noOfLines={1}
        >
          {address}
        </Text>
      </Center>
      <Center justifyContent='flex-end' textAlign='right'>
        {typeof commission === 'number' ? (
          <Text
            fontSize={{ md: '13px', base: '13px' }}
            fontWeight='500'
            lineHeight='100%'
            letterSpacing='-1.1%'
            color='#124A28'
            noOfLines={1}
          >
            {commission.toPrecision(2)}% Commission
          </Text>
        ) : null}
      </Center>
    </Grid>
  );
};

const PaymentPlanAvailabilityAndUnits = ({
  isPaymentPlanAvailable,
  availableUnits
}: {
  isPaymentPlanAvailable?: boolean;
  availableUnits?: number;
}) => {
  return (
    <HStack
      position='relative'
      width='100%'
      align={{ md: 'center', base: 'flex-end' }}
      justify='flex-start'
      flexDirection={{ md: 'row', base: 'row-reverse' }}
      gap={{ md: '6px', base: '4px' }}
      height={{ md: 'none', base: '8px' }}
      maxHeight={{ md: 'none', base: '8px' }}
      style={
        {
          '--payment-availability-color': isPaymentPlanAvailable
            ? '#116932'
            : '#A5A5A5',
          '--available-units-color': '#DC2626'
        } as React.CSSProperties
      }
    >
      <Center
        gap='4px'
        height={{ md: 'auto', base: '28px' }}
        roundedLeft={{ md: 'none', base: '4px' }}
        bg={{ md: 'transparent', base: '#16A34A' }}
        color={{ md: 'var(--payment-availability-color)', base: 'white' }}
        px={{ md: '0', base: '12px' }}
      >
        <Center
          boxSize='28px'
          minWidth='28px'
          rounded='full'
          bg='var(--payment-availability-color)'
          color='white'
          fontSize='16px'
          display={{ md: 'flex', base: 'none' }}
        >
          <CardholderIcon />
        </Center>
        <Text
          fontSize='13px'
          fontWeight='600'
          lineHeight='20px'
          letterSpacing='-1.1%'
          color='inherit'
          textDecoration={{ md: 'underline', base: 'none' }}
        >
          {isPaymentPlanAvailable
            ? 'Payment Plan Available'
            : 'Payment Plan Unavailable'}
        </Text>
      </Center>
      {typeof availableUnits === 'number' ? (
        <HStack
          align='center'
          ml='4px'
          gap='8px'
          color={{ md: 'var(--available-units-color)', base: 'white' }}
          height={{ md: 'auto', base: '28px' }}
          rounded={{ md: 'none', base: '4px' }}
          bg={{ md: 'transparent', base: 'var(--available-units-color)' }}
          px={{ md: '0', base: '12px' }}
        >
          <Icon
            as={FaCircleIcon}
            display={{ md: 'block', base: 'none' }}
            fontSize='10px'
            color='inherit'
          />
          <Text
            fontSize='13px'
            fontWeight='500'
            lineHeight='20px'
            letterSpacing='-1.1%'
            color='inherit'
          >
            {`${Math.max(0, availableUnits)} ${availableUnits === 1 ? 'unit' : 'units'}`}
          </Text>
        </HStack>
      ) : null}
    </HStack>
  );
};
