import { createContext } from 'react';
import { RealtorsPortalProviderProps } from './LibraryProvider';

export interface StateContextProviderProps extends RealtorsPortalProviderProps {}
export const StateContext = createContext<Partial<StateContextProviderProps>>({
  accessToken: ``,
  baseRoute: `/realtors`
});
export const StateContextProvider = ({
  children,
  ...props
}: StateContextProviderProps) => {
  return (
    <StateContext.Provider value={{ ...props }}>
      {children}
    </StateContext.Provider>
  );
};
