import { fetchReferralInfo } from '@/src/api/referrals';
import { ReferralAdditionalInfo } from '@/src/components/referrals/ReferralAdditionalInfo';
import { ReferralNextOfKin } from '@/src/components/referrals/ReferralNextOfKin';
import { ReferralInspection } from '@/src/components/referrals/ReferralInspections';
import { ReferralPortfolio } from '@/src/components/referrals/ReferralPortfolio';
import { LayoutWrapper } from '@/src/layout/Wrapper';
import { Center, Flex, Spinner, Stack } from '@chakra-ui/react';
import { useQuery } from '@tanstack/react-query';
import { NAVBAR_HEIGHT } from '@/src/layout/Navbar';
import { FOOTER_HEIGHT } from '@/src/layout/Footer';
import ReferralProfileSidebar from '@/src/components/referrals/ReferralProfileSidebar';

export const ReferralsPage = ({ id }: { id: string }) => {
  const referralData = useQuery<any>({
    queryKey: ['referralData', id],
    queryFn: () => fetchReferralInfo(id),
    enabled: !!id
  });

  const user = referralData?.data?.data?.user_info;
  const portfolio = referralData?.data?.data?.customer_investments;
  const next_of_kin = referralData?.data?.data?.next_of_kin;
  const inspection_requests = referralData?.data?.data?.inspection_requests;

  return (
    <LayoutWrapper>
      <Flex gap={`45px`}>
        <ReferralProfileSidebar
          data={user}
          isLoading={referralData?.isLoading}
        />
        <Stack
          w={`100%`}
          gap={{ base: `40px` }}
          py={{ base: `40px` }}
          pr={{ base: '20px', md: `95px` }}
          fontWeight={`600`}
          fontSize={`20px`}
          lineHeight={`100%`}
          letterSpacing={`-2%`}
        >
          {referralData?.isLoading ? (
            <Center
              width={`100%`}
              border={'1px solid'}
              borderColor={'#E4E4E7'}
              borderRadius={`12px`}
              minH={`600px`}
              p={`20px`}
            >
              <Spinner size={`xl`} />
            </Center>
          ) : (
            <>
              <ReferralPortfolio portfolio={portfolio} />
              <ReferralInspection inspection_requests={inspection_requests} />
              <ReferralAdditionalInfo userInfo={user} />
              <ReferralNextOfKin nextOfKinList={next_of_kin} />
            </>
          )}
        </Stack>
      </Flex>
    </LayoutWrapper>
  );
};
