import countries from "@/src/constants/country";
import HoverText from "@/src/uilib/hoverOnText";
import { HStack, Text } from "@chakra-ui/react";

export const formatNumberWithCommas = (prop, options = {}) => {
  const number = parseFloat(prop);

  if (!isNaN(number) && isFinite(number)) {
    return number.toLocaleString("en-US", options);
  }
  return "";
};

export const formatToCurrency = (amount, curr) => {
  if (typeof window === "undefined") return;
  const defaultCurrency =
    curr ||
    (localStorage.getItem("baseCurrency") !== "undefined" &&
      localStorage.getItem("baseCurrency"))
      ? JSON.parse(localStorage.getItem("baseCurrency"))
      : "USD";

  const defaultCountry =
    localStorage.getItem("baseCountry") !== "undefined" &&
    localStorage.getItem("baseCurrency")
      ? JSON.parse(localStorage.getItem("baseCountry"))
      : "United States Of America";
  const locale =
    countries.find((item) => item.name === defaultCountry)?.locale || "en-US";
  const baseCurrency = curr || defaultCurrency;

  try {
    const formattedAmount =
      amount && typeof amount === "string"
        ? Number(amount.replace(/\,/g, ""))
        : Number(amount?.toString()?.replace(/\,/g, ""));

    let formattedString = formattedAmount.toLocaleString(locale, {
      style: "currency",
      currency: baseCurrency,
      minimumFractionDigits: 2,
      maximumFractionDigits: 2,
    });

    if (formattedString.includes("NaN")) {
      throw new Error("-");
    }
    return formattedString;
  } catch (error) {
    console.error(error);
    return "-";
  }
};

export const FormatToColorfulCurrency = ({
  amount,
  curr = "naira",
  decimalStyle,
  condition = false,
  excludeCurrency = false,
  wrapper,
  lens = 17,
  fontSize,
  fontWeight,
  lineHeight = "100%",
  ...rest
}) => {
  if (typeof window === "undefined") return;
  const defaultCurrency =
    localStorage.getItem("baseCurrency") !== "undefined" &&
    localStorage.getItem("baseCurrency")
      ? JSON.parse(localStorage.getItem("baseCurrency"))
      : "USD";

  const defaultCountry =
    localStorage.getItem("baseCountry") !== "undefined" &&
    localStorage.getItem("baseCurrency")
      ? JSON.parse(localStorage.getItem("baseCountry"))
      : "United States Of America";
  const locale =
    countries.find((item) => item.name === defaultCountry)?.locale || "en-US";
  const formatCurrency = (value) => {
    const formatter = new Intl.NumberFormat(locale, {
      style: "currency",
      currency: defaultCurrency,
      minimumFractionDigits: 2,
      maximumFractionDigits: 2,
    });

    const parts = formatter.formatToParts(value);
    let result = [];
    parts.forEach((item) => {
      if (item.type === "currency") {
        result[0] = item.value;
      } else if (item.type === "integer" || item.type === "group") {
        result[1] = item.value;
      }
    });

    let integerPart = `${result[0]}${result[1]}`;

    const decimalPart =
      parts.find((part) => part.type === "fraction")?.value || "00";
    const decimalSeparator =
      parts.find((part) => part.type === "decimal")?.value || ".";

    let formattedString = formatter.format(value);
    // if (condition !== 'no space') {
    //   formattedString = formattedString.replace(/(\D)(\d)/, '$1 $2');
    //   integerPart = integerPart.replace(/(\D)(\d)/, '$1 $2');
    // }
    if (excludeCurrency) {
      formattedString = formattedString.replace(/^[^\d]+/, "").trim();
      integerPart = integerPart.replace(/^[^\d]+/, "").trim();
    }

    return [integerPart, decimalPart, decimalSeparator, formattedString];
  };
  const escapeRegExp = (string) => {
    return string.replace(/[.*+?^${}()|[\]\\]/g, "\\$&");
  };

  try {
    const formattedAmount =
      amount && typeof amount === "string"
        ? Number(amount.replace(/\,/g, ""))
        : Number(amount?.toString()?.replace(/\,/g, ""));
    if (isNaN(formattedAmount)) throw new Error("Invalid amount");

    const [integerPart, decimalPart, decimalSeparator, formattedString] =
      formatCurrency(formattedAmount);

    const separatorRegex = new RegExp(
      `(${escapeRegExp(decimalSeparator)}\\d{2})`
    );
    const parts = formattedString.split(separatorRegex).filter(Boolean);
    const decimalSeparatorRegex = new RegExp(
      `^${escapeRegExp(decimalSeparator)}\\d{2}$`
    );
    return (
      <HStack alignItems="center" spacing="none" {...wrapper}>
        <HoverText
          text={formattedString.length > lens ? formattedString : parts?.[0]}
          lens={lens}
          as="span"
          pr="0px"
          fontSize={fontSize}
          fontWeight={fontWeight}
          lineHeight={lineHeight}
          {...rest}
        />
        {formattedString.length > lens ? null : (
          <Text
            as="span"
            color="lightgrey"
            fontSize={fontSize}
            fontWeight={fontWeight}
            lineHeight={lineHeight}
            {...decimalStyle}
          >
            {parts?.[1]}
          </Text>
        )}
      </HStack>
    );
  } catch (error) {
    const [integerPart, decimalPart, decimalSeparator, formattedString] =
      formatCurrency(0);

    const separatorRegex = new RegExp(
      `(${escapeRegExp(decimalSeparator)}\\d{2})`
    );
    const parts = formattedString.split(separatorRegex).filter(Boolean);
    const decimalSeparatorRegex = new RegExp(
      `^${escapeRegExp(decimalSeparator)}\\d{2}$`
    );

    return (
      <Text
        w="fit-content"
        fontSize={fontSize}
        fontWeight={fontWeight}
        lineHeight={lineHeight}
        {...rest}
      >
        {parts.map((part, index) => {
          if (decimalSeparatorRegex.test(part)) {
            return (
              <Text
                key={index}
                as="span"
                color="lightgrey"
                fontSize={fontSize}
                fontWeight={fontWeight}
                lineHeight={lineHeight}
                {...decimalStyle}
              >
                {part}
              </Text>
            );
          } else {
            return (
              <Text key={index} as="span">
                {part}
              </Text>
            );
          }
        })}
      </Text>
    );
  }
};
