import { Box, ChakraProps } from "@chakra-ui/react";
import { APIProvider, Map, type MapProps } from "@vis.gl/react-google-maps";
import { cubicBezier, motion } from "framer-motion";
import { MapContent } from "./mapContent";

export interface MarkerProp {
  lng: number;
  lat: number;
  price?: number;
  units?: number;
}
interface InHouseMapProps extends MapProps {
  markers?: Partial<MarkerProp>[];
  renderMarker?: (prop: MarkerProp) => React.ReactNode;
  onZoomIn?: (zoom?: number) => void;
  onZoomOut?: (zoom?: number) => void;
  containerStyling: ChakraProps;
}

const MotionBox = motion.create(Box);

export default function InHouseMap(mapProps: InHouseMapProps) {
  return (
    <APIProvider apiKey={process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY ?? ""}>
      <MapContainer {...mapProps} />
    </APIProvider>
  );
}

function MapContainer({
  containerStyling = {},
  markers = [],
  renderMarker,
  disableDefaultUI = true,
  gestureHandling = "greedy",
  mapId = "Map_id",
  zoom,
  ...mapProps
}: InHouseMapProps) {
  return (
    <MotionBox
      as={motion.div}
      height="auto"
      width="100%"
      overflow="hidden"
      transition={
        {
          duration: 0.5,
          ease: cubicBezier(0.3, 1, 0.16, 1),
        } as any
      }
      {...containerStyling}
    >
      <Map
        mapId={mapId}
        disableDefaultUI={disableDefaultUI}
        gestureHandling={gestureHandling}
        {...mapProps}
      >
        <MapContent
          zoom={zoom}
          markers={markers as MarkerProp[]}
          CustomMarker={renderMarker}
        />
      </Map>
    </MotionBox>
  );
}

const ONE_THOUSAND = 1000;
const ONE_MILLION = 1_000_000;
const ONE_BILLION = 1_000_000_000;
const ONE_TRILLION = 1_000_000_000_000;

export function shortenPrice(value: number) {
  if (value < ONE_THOUSAND) return value;
  else if (value >= ONE_THOUSAND && value < ONE_MILLION)
    return `${divideFloat(value, ONE_THOUSAND)}k`;
  else if (value >= ONE_MILLION && value < ONE_BILLION)
    return `${divideFloat(value, ONE_MILLION)}M`;
  else if (value >= ONE_BILLION && value < ONE_TRILLION)
    return `${divideFloat(value, ONE_BILLION)}B`;
  else if (value >= ONE_TRILLION) return `${divideFloat(value, ONE_TRILLION)}T`;
  else return "";
}

function divideFloat(numerator: number, denominator: number, decimals = 1) {
  return Math.min(
    999.9,
    parseFloat((numerator / denominator).toFixed(decimals))
  );
}
