import {
  AiAssistantIcon,
  CalendarIcon,
  GeneralFeedbackIcon,
  LogoutIcon,
  MoreOptionsIcon,
  OurOfferingsIcon,
  PrivacyPolicyIcon,
  ProfileIcon,
  ReferralsIcon,
  ReportBugIcon,
  RequestIcon,
  SuggestIdeaIcon,
  TermsIcon,
  TransactionsIcon
} from '@/src/components/assets/navbar/sidemenu';
import { AppRouterInstance } from 'next/dist/shared/lib/app-router-context.shared-runtime';
import { NextRouter } from 'next/router';
import { Dispatch, MouseEventHandler, ReactElement } from 'react';

export interface SidebarMenuInterface {
  title: string;
  icon: ReactElement;
  active?: boolean;
  subMenu?: boolean;
  isDisabled?: boolean;
  hide?: boolean;
  onClick?: () => void;
  href?: string;
  externalLink?: boolean;
  openNewDrawer?: boolean;
}

export const activeLinkColors = {
  color: '#3636E2',
  bgColor: '#EBEBFB'
};

export const getMenuList = ({
  pathname,
  activeMenu
}: {
  pathname: string;
  activeMenu: string;
}) => {
  return [
    {
      title: 'Our Offerings',
      icon: (
        <OurOfferingsIcon
          color={
            pathname?.includes(`/realtors/offerings`)
              ? activeLinkColors?.color
              : undefined
          }
        />
      ),
      active: pathname?.includes(`/realtors/offerings`),
      href: `/realtors/offerings`
    },
    {
      title: 'Transactions',
      icon: (
        <TransactionsIcon
          color={
            pathname?.includes(`/realtors/transactions`)
              ? activeLinkColors?.color
              : undefined
          }
        />
      ),
      active: pathname?.includes(`/realtors/transactions`),
      href: `/realtors/transactions`
    },
    {
      title: 'Referrals',
      icon: (
        <ReferralsIcon
          color={
            pathname?.includes(`/realtors/referrals`)
              ? activeLinkColors?.color
              : undefined
          }
        />
      ),
      active: pathname?.includes(`/realtors/referrals`),
      openNewDrawer: true
    },
    {
      title: 'Requests',
      icon: (
        <RequestIcon
          color={
            pathname?.includes(`/realtors/request`) || activeMenu === `requests`
              ? activeLinkColors?.color
              : undefined
          }
        />
      ),
      active:
        pathname?.includes(`/realtors/request`) || activeMenu === `requests`,
      href: `/realtors/request`
    },
    {
      title: 'Profile',
      icon: (
        <ProfileIcon
          color={
            pathname?.includes(`/realtors/profile`)
              ? activeLinkColors?.color
              : undefined
          }
        />
      ),
      active: pathname?.includes(`/realtors/profile`),
      href: `/realtors/profile`
    },
    {
      title: 'More',
      subMenu: true,
      icon: (
        <MoreOptionsIcon
          color={
            activeMenu === 'more options' ? activeLinkColors?.color : undefined
          }
        />
      ),
      active: activeMenu === 'more options'
    }
  ];
};

export const getSubMenuList = ({
  router,
  pathname,
  query,
  activeMenu
}: {
  router: AppRouterInstance;
  pathname: string;
  query: any;
  activeMenu: string;
}) => {
  switch (activeMenu) {
    case 'requests':
      return [
        {
          title: 'Inspection Requests',
          icon: (
            <RequestIcon
              color={
                pathname?.includes(`/realtors/request`) &&
                !query?.history &&
                query?.tab == 'inspection'
                  ? activeLinkColors?.color
                  : undefined
              }
            />
          ),
          active:
            pathname?.includes(`/realtors/request`) &&
            !query?.history &&
            query?.tab == 'inspection',
          onClick: () => {
            router.push(`/realtors/request?&tab=inspection`);
          }
        },
        {
          title: 'Inspection History',
          icon: (
            <RequestIcon
              color={
                pathname?.includes(`/realtors/request`) &&
                query?.history == 'true' &&
                query?.tab == 'inspection'
                  ? activeLinkColors?.color
                  : undefined
              }
            />
          ),
          active:
            pathname?.includes(`/realtors/request`) &&
            query?.history == 'true' &&
            query?.tab == 'inspection',
          onClick: () => {
            router.push(`/realtors/request?history=true&tab=inspection`);
          }
        },
        {
          title: 'Commission Requests',
          icon: (
            <RequestIcon
              color={
                pathname?.includes(`/realtors/request`) &&
                !query?.history &&
                query?.tab == 'commission'
                  ? activeLinkColors?.color
                  : undefined
              }
            />
          ),
          active:
            pathname?.includes(`/realtors/request`) &&
            !query?.history &&
            query?.tab == 'commission',
          onClick: () => {
            router.push(`/realtors/request?tab=commission`);
          }
        },
        {
          title: 'Commission History',
          icon: (
            <RequestIcon
              color={
                pathname?.includes(`/realtors/request`) &&
                query?.history == 'true' &&
                query?.tab == 'commission'
                  ? activeLinkColors?.color
                  : undefined
              }
            />
          ),
          active:
            pathname?.includes(`/realtors/request`) &&
            query?.history == 'true' &&
            query?.tab == 'commission',
          onClick: () => {
            router.push(`/realtors/request?history=true&tab=commission`);
          }
        }
      ];
    case 'more':
      return [
        {
          title: 'AI Assistant',
          icon: <AiAssistantIcon />,
          active: false
        },
        {
          title: 'Calendar',
          icon: <CalendarIcon />,
          active: false
        },
        {
          title: 'Report a bug',
          icon: <ReportBugIcon />,
          active: false
        },
        {
          title: 'Suggest an idea',
          icon: <SuggestIdeaIcon />,
          active: false
        },
        {
          title: 'General Feedback',
          icon: <GeneralFeedbackIcon />,
          active: false
        }
      ];
    default:
      return [];
  }
};

export const getMenuFooterList = () => {
  return [
    {
      title: 'Terms of Use',
      icon: <TermsIcon />,
      active: false
    },
    {
      title: 'Privacy Policy',
      icon: <PrivacyPolicyIcon />,
      active: false
    },
    {
      title: 'Logout',
      icon: <LogoutIcon />,
      active: false
    }
  ];
};
