import { ChakraProvider, extendTheme } from '@chakra-ui/react';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import { StateContextProvider } from './StateContext';
import { libraryTheme } from '../utils/theme';

// 1. Create a segregated Query Client so you don't mess with the Host App's cache
const libraryQueryClient = new QueryClient({
  defaultOptions: { queries: { staleTime: 1000 * 60 } }
});

export interface RealtorsPortalProviderProps {
  children: React.ReactNode;
  baseRoute: string;
  accessToken: string;
  themeConfig?: Record<string, any>;
}

export const RealtorsPortalProvider = ({
  children,
  themeConfig,
  ...rest
}: RealtorsPortalProviderProps) => {
  const theme = extendTheme({ ...libraryTheme, ...themeConfig });
  return (
    <QueryClientProvider client={libraryQueryClient}>
      <ChakraProvider theme={theme}>
        <StateContextProvider {...rest}>{children}</StateContextProvider>
      </ChakraProvider>
    </QueryClientProvider>
  );
};
