import { extendTheme } from '@chakra-ui/react';

// 2. Define your specific theme overrides if needed
export const libraryTheme = extendTheme({
  fonts: { heading: 'Inter', body: 'Inter' },
  components: {
    Button: {
      baseStyle: {
        outline: `none`,
        _hover: {
          opacity: `1`
        },
        _active: {
          opacity: `1`
        },
        _focus: {
          outline: `none`
        },
        _focusVisible: {
          outline: `none`
        }
      }
    },
    Input: {
      baseStyle: {
        outline: `none`,
        _hover: {
          outline: `1px solid #000000`
        },
        _active: {
          outline: `1px solid #000000`
        },
        _focus: {
          outline: `1px solid #000000`
        },
        _focusVisible: {
          outline: `1px solid #000000`
        }
      }
    }
  }
});
