export const parseSearchParamsToObject = (searchParams, acceptableParams) => {
  const paramsObject = {};

  // Check if acceptableParams is provided and is an array of strings.
  const hasAcceptableParams = acceptableParams && acceptableParams.length > 0;

  searchParams.forEach((value, key) => {
    // **FIXED:** Use .includes() to check if the key is in the acceptableParams array.
    // The parameter is accepted if:
    // 1. No acceptableParams filter is provided (hasAcceptableParams is false)
    // 2. OR the key is found within the acceptableParams array.
    if (!hasAcceptableParams || acceptableParams.includes(key)) {
      if (key in paramsObject) {
        // Parameter is a duplicate, convert to/push to an array
        if (Array.isArray(paramsObject[key])) {
          paramsObject[key].push(value);
        } else {
          paramsObject[key] = [paramsObject[key], value];
        }
      } else {
        // First occurrence of the parameter
        paramsObject[key] = value;
      }
    }
  });

  return paramsObject;
};
