import { fetchAccountInfo } from '@/src/api/auth';
import { accountTransactions } from '@/src/api/transactions';
import { GeneralFeedbackIcon } from '@/src/components/assets/navbar/sidemenu';
import { CommissionSidePanel } from '@/src/components/transactions/commissions/CommissionsSidePanel';
import { TransactionEntry } from '@/src/components/transactions/TransactionEntry';
import { LayoutWrapper } from '@/src/layout/Wrapper';
import {
  Center,
  Flex,
  Heading,
  Spinner,
  Stack,
  StackDivider,
  Text,
  VStack
} from '@chakra-ui/react';
import { useQuery } from '@tanstack/react-query';

export const TransactionsPage = () => {
  const { data: transactionsData, isLoading } = useQuery({
    queryKey: ['account-transactions'],
    queryFn: accountTransactions
  });

  console.log({ transactionsData });

  const transactions: any = transactionsData?.data?.data.filter(
    (nullTrans: any) =>
      nullTrans?.transaction_type === 'commission' &&
      nullTrans?.connected_request !== null
  );

  return (
    <LayoutWrapper>
      <Flex gap={`20px`}>
        <Stack
          w={`100%`}
          p={{ base: '32px 40px', xl: `42px 120px` }}
          gap={{ base: `24px` }}
        >
          <Heading
            fontWeight={{ base: `600` }}
            fontSize={{ base: `32px` }}
            letterSpacing={{ base: `-2%` }}
          >
            Transactions
          </Heading>

          <VStack
            align={`stretch`}
            w={`100%`}
            border={`0.75px solid`}
            borderColor={'#E8E8E8'}
            borderRadius={`12px`}
            gap={`0px`}
            divider={
              <StackDivider margin={`0px !important`} borderColor={'#E8E8E8'} />
            }
          >
            {isLoading ? (
              <Center height={`400px`}>
                <Spinner size={`xl`} />
              </Center>
            ) : !transactions?.length ? (
              <VStack
                w={`100%`}
                p={`42px`}
                height={`400px`}
                justify={'center'}
                gap={`4px`}
              >
                <GeneralFeedbackIcon boxSize={`36px`} />
                <Text
                  fontSize={`16px`}
                  fontWeight={`600`}
                  textTransform={`uppercase`}
                >
                  Nothing Found
                </Text>
                <Text fontSize={`14px`} fontWeight={`400`}>
                  No results found
                </Text>
              </VStack>
            ) : (
              transactions?.map((request: any) => (
                <>
                  <TransactionEntry key={request?.id} data={request} />
                </>
              ))
            )}
          </VStack>
        </Stack>
        <CommissionSidePanel />
      </Flex>
    </LayoutWrapper>
  );
};
