"use client";

import { UnitPageComponent } from "@/src/components/listingsProfile/ui-lib/unit/UnitPageComponent";
import { LayoutWrapper } from "@/src/layout/Wrapper";
import { useParams } from "next/navigation";

export interface UnitProfilePageProps {
  apiKey: string; // Forces host to pass required config
  userName?: string;
}

export const UnitProfilePage = ({ apiKey, userName }: UnitProfilePageProps) => {
  const param = useParams();
  const id = (param.id as string) ?? "";
  return (
    <LayoutWrapper sidebarMenuStyle="expandable">
      <UnitPageComponent unit_id={id} />
    </LayoutWrapper>
  );
};
