'use client';

import { LayoutWrapper } from '@/src/layout/Wrapper';
import {
  Box,
  Flex,
  List,
  ListItem,
  Text,
  useMediaQuery,
  VStack
} from '@chakra-ui/react';
import { DUMMY_LISTINGS } from './dummy_data';
import Map from '@/src/components/map';
import { ListingCard } from '@/src/components/listing/card/ListingCard';
import { Filters } from '@/src/components/filters';

export interface ListingsPageProps {
  apiKey: string; // Forces host to pass required config
  userName?: string;
}

export const ListingsPage = ({ apiKey, userName }: ListingsPageProps) => {
  const markers = DUMMY_LISTINGS.map(
    ({ listing_price, longitude, latitude }) => ({
      price: listing_price,
      lng: longitude,
      lat: latitude
    })
  );

  return (
    <LayoutWrapper>
      <Flex gap='0' flex='1'>
        <Text>Hi</Text>
        <VStack
          width='100%'
          maxWidth={{ lg: '836px', base: 'none' }}
          p='0'
          gap='0'
        >
          <Filters />
          <List
            width='100%'
            display='flex'
            flexDirection='column'
            gap='20px'
            p={{
              lg: '0 32px 32px 40px',
              md: '0 24px 24px',
              base: '0 20px 24px'
            }}
          >
            {DUMMY_LISTINGS.map((listing) => (
              <ListItem key={listing.id} width='100%'>
                <ListingCard {...listing} />
              </ListItem>
            ))}
          </List>
        </VStack>
        <Box
          flex='1'
          display={{ lg: 'block', base: 'none' }}
          minWidth={{ lg: '480px', base: '360px' }}
        >
          <Map
            markers={markers}
            defaultZoom={20}
            defaultCenter={{ lat: 6.6173, lng: 3.3553 }}
            containerStyling={{
              width: '100%',
              height: 'calc(100dvh - 64px - 49px)',
              position: 'sticky',
              top: '64px'
            }}
          />
        </Box>
      </Flex>
    </LayoutWrapper>
  );
};
