import { useContext, useState } from 'react';
import {
  Box,
  Button,
  Flex,
  Input,
  Stack,
  Text,
  useToast
} from '@chakra-ui/react';
import { useMutation } from '@tanstack/react-query';
import { store_name } from '@/src/constants/routes';
import { realtorLogin } from '@/src/api/auth';
import { StateContext } from '@/src/providers/StateContext';

export const BaseLogin = () => {
  const [email, setEmail] = useState('');
  const [loggedIn, setLoggedIn] = useState(false);
  const storeName = store_name();
  const toast = useToast();

  const { baseRoute } = useContext(StateContext);

  const handleSubmit = () => {
    mutate({ email, store_name: storeName });
  };

  const { mutate, isPending } = useMutation(
    (formData: { email: string; store_name: string }) => realtorLogin(formData),
    {
      onSuccess: (res: any) => {
        if (
          res?.response?.data?.action == 'signup' ||
          res?.response?.data?.action == 'not_customer'
        ) {
          toast({
            status: 'error',
            title: 'Error',
            description:
              'Account registeration currently disabled. Login with a registered account'
          });
        } else if (res?.data?.action == 'login') {
          setLoggedIn(true);

          console.log({ baseRoute, res });
          toast({
            status: 'success',
            title: 'Success',
            description: 'Logged In Succesfully'
          });
          if (res?.data?.link) {
            location.assign(`${baseRoute}?${res?.data?.link?.split(`?`)?.[1]}`);
          }
        } else {
          toast({
            status: 'error',
            title: 'Error',
            description: 'There was a problem authenticating this email'
          });
        }
      },
      onError: (err) => {
        toast({
          status: 'error',
          title: 'Error',
          description: 'There was a problem authenticating this email'
        });
      }
    }
  );

  return (
    <Box
      maxW='440px'
      w={{ base: `100%`, lg: `440px` }}
      bg='#f5f5f5'
      px={{ base: `24px`, md: '40px' }}
      py='32px'
      borderRadius={5}
      boxShadow={
        '0px 4px 8px -2px rgba(16, 24, 40, 0.10), 0px 2px 4px -2px rgba(16, 24, 40, 0.06)'
      }
    >
      <Flex h='full' direction='column' justify={'center'} align='center'>
        <Text fontSize={'23px'} fontWeight={600} color='#000000'>
          Login
        </Text>
        <Stack
          w={`100%`}
          gap={{ base: `24px`, md: `16px` }}
          mt={`8px`}
          textAlign={`center`}
          align={`center`}
        >
          <Text
            color='#5b5b5b'
            fontSize={{ base: '13px', lg: '16px' }}
            lineHeight={`140%`}
          >
            Enter realtor email address.
          </Text>
          <Input
            type='email'
            name='email'
            id='email'
            onChange={(e) => setEmail(e.target.value)}
            value={email}
            placeholder='Email Address'
            _placeholder={{ fontSize: '13px' }}
            fontSize='16px'
            padding={{ base: `12px 14px`, md: '14px 15px' }}
            height='100%'
            lineHeight='140%'
          />

          <Button
            w={`100%`}
            borderRadius={`full`}
            bg={`#000`}
            color={`#fff`}
            fontSize={`12px`}
            lineHeight={`150%`}
            textTransform={`capitalize`}
            fontWeight={`400`}
            onClick={handleSubmit}
            isLoading={isPending}
            p={`8px`}
          >
            Proceed
          </Button>
        </Stack>
      </Flex>
    </Box>
  );
};
